.banner{width:100%;height:calc(100vh - var(--header-height) - 104px);min-height:800px;max-height:1000px;position:relative;overflow:hidden}.banner>.container{display:flex;flex-direction:row;align-items:center;justify-content:space-between;position:relative}.banner>.container .text-box{position:relative;z-index:1}.banner>.container .text-box .achievement{width:360px;height:36px;display:flex;flex-direction:row;align-items:center;justify-content:flex-start;background:#e2eaed;border-radius:100rem}.banner>.container .text-box .achievement .icon{width:40px;height:100%;display:flex;align-items:center;justify-content:center;background-color:#feaa25;border-radius:inherit}.banner>.container .text-box .achievement .icon img{width:20px;height:20px;margin-right:1px}.banner>.container .text-box .achievement .content{margin-left:10px;font-size:18px;font-weight:500;color:var(--title-color);margin-top:1px}.banner>.container .text-box>.title{width:530px;height:140px;color:var(--title-color);font-size:60px;font-weight:700;line-height:1.3;margin-top:20px}.banner>.container .text-box .desc{width:530px;color:var(--text-color);font-size:18px;line-height:1.8;margin-top:30px}.banner>.container .text-box .button-list{margin-top:40px}.banner>.container .text-box .button-list .watch-demo-video-button{color:#2500f9;font-size:16px;font-weight:600;margin-left:27px;background-color:transparent}.banner>.container .banner-img{width:400px;height:800px;position:relative;z-index:1;object-fit:contain;align-self:flex-start}.banner>.background{height:100%;position:absolute;top:25px;right:44%;transform:translateX(45%);z-index:1}.banner .background-left{width:700px;position:absolute;top:20px;left:0}.banner .background-bottom{width:100vw;height:250px;position:absolute;right:0;bottom:0}
