.features-content{width:440px;margin-left:40px}.features-content>.title{color:var(--title-color);line-height:1.2;font-size:36px;font-weight:600}.features-content>.description{color:var(--text-color);font-size:16px;margin-top:16px;line-height:1.6}.features-content .feature-list{margin-top:90px}.features-content .feature-list li{display:flex;flex-direction:row;align-items:center;justify-content:space-between}.features-content .feature-list li:not(:first-child){margin-top:66px}.features-content .feature-list li img{width:90px;height:90px}.features-content .feature-list li div{margin-left:32px}.features-content .feature-list li div h4{color:#f55767;font-size:20px;font-weight:700}.features-content .feature-list li div p{color:var(--text-color);font-size:16px;margin-top:10px}
