.ultimate-features-content{width:680px;flex-shrink:0;margin-left:46px}.ultimate-features-content .description-container h3{color:var(--title-color);font-size:36px;font-weight:700;line-height:1.3}.ultimate-features-content .description-container p{color:var(--text-color);font-size:16px;line-height:1.6;margin-top:30px}.ultimate-features-content .features-list-container{margin-top:60px}.ultimate-features-content .features-list-container .features-list{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr 1fr;gap:60px 90px}.ultimate-features-content .features-list-container .features-list li img{width:88px;height:88px}.ultimate-features-content .features-list-container .features-list li h4{color:var(--title-color);font-size:20px;font-weight:700;line-height:1.3;margin-top:20px}.ultimate-features-content .features-list-container .features-list li p{color:var(--text-color);font-size:16px;line-height:1.5;margin-top:12px}.ultimate-features-content .see-all-button{margin-top:55px}
