.payment-option-card-container{margin-top:40px}.payment-option-card-container .option-card-list{display:flex;flex-direction:row;align-items:flex-start;justify-content:flex-start}.payment-option-card-container .option-card-list>li{width:317px;padding-bottom:25px;border-radius:9px;overflow:hidden}.payment-option-card-container .option-card-list>li .card-header{width:100%;height:234px;display:flex;flex-direction:column;align-items:center;justify-content:center;background-image:linear-gradient(0deg, #f4f9ff, transparent)}.payment-option-card-container .option-card-list>li .card-header .price{display:flex;flex-direction:row;align-items:baseline;justify-content:flex-start}.payment-option-card-container .option-card-list>li .card-header .price h4{color:var(--title-color);font-size:60px;font-weight:700;line-height:1.3;transform:translateY(4px)}.payment-option-card-container .option-card-list>li .card-header .price p{color:var(--text-color);font-size:18px;line-height:1.6;color:#999ca5}.payment-option-card-container .option-card-list>li .card-header .title{color:var(--title-color);font-size:20px;font-weight:700;line-height:1.3;margin-top:24px}.payment-option-card-container .option-card-list>li .card-header .description{color:var(--text-color);font-size:16px;line-height:1.6;color:#999ca5;margin-top:10px}.payment-option-card-container .option-card-list>li .card-body{padding:40px 70px 43px}.payment-option-card-container .option-card-list>li .card-body .permission-list{display:grid;grid-template-rows:repeat(4, 1fr);row-gap:24px}.payment-option-card-container .option-card-list>li .card-body .permission-list li{color:var(--text-color);font-size:16px;line-height:1.6;color:#999ca5;text-align:center}.payment-option-card-container .option-card-list>li .card-footer{display:flex;flex-direction:column;align-items:center;justify-content:flex-start}.payment-option-card-container .option-card-list>li.free{box-shadow:3.837px 54.866px 62px 0px rgba(144,163,177,0.2)}.payment-option-card-container .option-card-list>li.premium{border:2px solid #ebebeb;margin-left:40px}.payment-option-card-container .option-card-list>li.premium .start-trial-button{color:#2500f9;font-size:16px;border:none;outline:none;margin-top:30px;background-color:transparent}
