.reason-choose-list-container{padding:100px 90px 180px}.reason-choose-list-container .reason-choose-list{display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 1fr);gap:90px}.reason-choose-list-container .reason-choose-list li{display:flex;flex-direction:column;align-items:center;justify-content:flex-start;text-align:center}.reason-choose-list-container .reason-choose-list li img{width:88px;height:88px}.reason-choose-list-container .reason-choose-list li h4{color:var(--title-color);font-size:20px;font-weight:700;margin-top:36px}.reason-choose-list-container .reason-choose-list li p{color:var(--text-color);font-size:16px;margin-top:24px}
