.advantage-list-container{margin-top:110px}.advantage-list-container .advantage-list{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:120px}.advantage-list-container .advantage-list li.payout h4{color:#f55767}.advantage-list-container .advantage-list li.network h4{color:#2563ff}.advantage-list-container .advantage-list li.rewards h4{color:#40975f}.advantage-list-container .advantage-list li img{width:88px;height:88px}.advantage-list-container .advantage-list li h4{font-size:20px;font-weight:700;margin-top:30px}.advantage-list-container .advantage-list li p{color:var(--text-color);line-height:1.5;font-size:16px;margin-top:16px}
