.question-list-container{margin-top:65px}.question-list-container .question-list li{position:relative;background:#f5f7fa;padding:20px 40px}.question-list-container .question-list li:not(:first-child){margin-top:10px}.question-list-container .question-list li.active{padding:40px 70px 40px 40px}.question-list-container .question-list li>h4{color:var(--title-color);font-size:20px;font-weight:700;line-height:1.3;font-weight:600}.question-list-container .question-list li>p{color:var(--text-color);font-size:16px;line-height:1.6;margin-top:35px}.question-list-container .question-list li>svg{width:20px;height:20px;position:absolute;top:50%;right:40px;transform:translateY(-50%)}.question-list-container .question-list li>svg path{fill:#242f51}
