.footer-contact{margin-top:52px}.footer-contact__list{display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.footer-contact__list li:not(:first-child){margin-left:20px}.footer-contact__list li.facebook svg{height:30px}.footer-contact__list li.twitter svg{height:30px}.footer-contact__list li.github svg{width:30px;height:30px;background-color:rgba(255,255,255,0.25);padding:4px;border-radius:4px}.footer-contact__list li svg path{fill:#273043}
